/* --- 1. Global Reset and Variables --- */
:root {
    --color-sidebar-bg: #004d40; /* Dark Green */
    --color-main-bg: #e0e0e0;     /* Light Gray */
    --color-text-light: #ffffff;  /* White */
    --color-text-dark: #333333;   /* Dark Text */
    --color-highlight-green: #2ecc71; /* Accent Green for logo/links */
    --sidebar-width: 250px;
    --font-family-primary: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body, html, #root {
    height: 100%;
    font-family: var(--font-family-primary);
}

/* --- 2. Main Layout Structure --- */

#app-container {
    display: flex;
    height: 100vh; /* Full viewport height */
}

/* --- 3. Sidebar Styling (Dark Green Column) --- */

#sidebar {
    width: var(--sidebar-width);
    background-color: var(--color-sidebar-bg);
    color: var(--color-text-light);
    display: flex;
    flex-direction: column;
    padding: 20px 0 20px 20px; /* Padding for content inside */
    position: fixed; /* Ensures it stays fixed during scrolling */
    height: 100%;
}

/* Logo and Header Section */
#logo-section {
    padding-bottom: 40px;
    line-height: 1.2;
    font-weight: bold;
    font-size: 1.2em;
}

.onboarding-hub-text {
    font-size: 0.7em;
    font-weight: normal;
    color: var(--color-highlight-green);
}

/* Navigation Links */
#main-nav {
    flex-grow: 1; /* Pushes the user footer to the bottom */
    display: flex;
    flex-direction: column;
    gap: 20px; /* Spacing between nav links */
    padding-top: 20px;
}

#main-nav a {
    color: var(--color-text-light);
    text-decoration: none;
    font-size: 1.1em;
    padding: 5px 0;
    transition: color 0.2s;
}

#main-nav a:hover {
    color: var(--color-highlight-green);
}

#main-nav a.active-nav {
    color: var(--color-highlight-green);
    font-weight: 600;
    position: relative;
}

#main-nav a.active-nav::before {
    content: '';
    position: absolute;
    left: -15px;
    top: 50%;
    transform: translateY(-50%);
    width: 4px;
    height: 28px;
    border-radius: 2px;
    background-color: var(--color-highlight-green);
}

/* --- 4. Sidebar Footer (User Info & Logout) --- */

#user-footer {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-top: 20px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.user-info {
    display: flex;
    flex-direction: column;
    font-size: 0.9em;
}

.settings-link {
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;
    font-size: 0.8em;
}

#logout-btn {
    background-color: transparent;
    border: 1px solid var(--color-text-light);
    color: var(--color-text-light);
    padding: 8px 15px;
    cursor: pointer;
    text-align: left;
    transition: background-color 0.2s;
    font-family: var(--font-family-primary);
}

#logout-btn:hover {
    background-color: rgba(255, 255, 255, 0.1);
}


/* --- 5. Main Content Area (Light Gray) --- */

#main-view {
    /* Offset the main view by the width of the fixed sidebar */
    margin-left: var(--sidebar-width); 
    flex-grow: 1;
    background-color: var(--color-main-bg);
    padding: 40px;
    min-height: 100vh;
}

/* Header/Greeting */
#greeting {
    color: var(--color-sidebar-bg); /* Use the dark green for the greeting */
    margin-bottom: 30px;
}

/* Content Card Styling */
.content-card {
    background-color: var(--color-text-light); /* White background */
    border-radius: 15px; /* Rounded corners */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    padding: 30px;
    min-height: 200px;
    width: 60%; /* Example width to match the prototype's box size */
}

/* --- Partners Page Specific Styles --- */
.partners-page {
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.partners-header .eyebrow {
    text-transform: uppercase;
    font-size: 0.85rem;
    letter-spacing: 0.08em;
    color: var(--color-sidebar-bg);
    margin-bottom: 8px;
}

.partners-header h2 {
    margin-bottom: 6px;
}

.view-info {
    color: #666;
}

.filters-card {
    width: 100%;
}

.partners-list-card {
    width: 100%;
}

.filters-row {
    display: flex;
    gap: 15px;
    align-items: center;
    flex-wrap: wrap;
}

.filters-row input[type="search"],
.filters-row select {
    flex: 1;
    min-width: 140px;
    padding: 12px 16px;
    border-radius: 10px;
    border: 1px solid #d9d9d9;
    font-size: 0.95rem;
}

.filters-row input[type="search"] {
    flex: 2;
}

.partner-row {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 12px;
    padding: 20px 0;
    border-bottom: 1px solid #f0f0f0;
}

.partner-row:last-child {
    border-bottom: none;
}

.partner-cell .label {
    font-size: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 0.08em;
    color: #7a7a7a;
    margin-bottom: 6px;
}

.partner-cell .value {
    font-size: 1rem;
    color: var(--color-text-dark);
    font-weight: 600;
}

.partner-name .value {
    font-size: 1.1rem;
}

.empty-state {
    color: #7a7a7a;
}